
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#login {
  text-align: center;
  width: 100%;
  padding: 0;
  padding-top: 64px;
}
#login h1 {
  margin-bottom: 64px;
  font-size: 3rem;
  color: #3F51B5;
}
#login .button {
  padding: 16px 64px;
  background: #3F51B5;
  border: 2px solid #3F51B5;
  border-radius: 4px;
}
#login > a, #login > a:hover, #login > a:visited {
  text-decoration: none;
  font-weight: bold;
  color: #FFFFFF;
}
#login .description {
  text-align: center;
  padding: 32px;
  color: #2c3e50;
}
#login .description a,
#login .description a:hover,
#login .description a:visited {
  text-decoration: none;
  font-weight: bold;
  color: #AD1457;
}
#login .description p {
  margin: 16px 0;
}

#container {
  background-color: #3F51B5;
}
#stocks {
  width: 100%;
}
#headline {
  width: 100%;
  text-align: center;
  padding: 64px 0;
  background-color: #3F51B5;
}
#headline h1 {
  margin-bottom: 32px;
  font-size: 3rem;
  color:#FFFFFF;
}
#headline .search input {
  padding: 10px;
  font-size: 1.3em;
  font-family: Arial, sans-serif;
  color: #aaa;
  border: 1px solid #ccc;
  margin: 0 0 20px;
  width: 300px;
  border-radius: 3px;
  -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.2);
}
#headline .search button,
#more-show button {
  padding: 8px 32px;
  border: 2px solid #3F51B5;
  background-color: #FFFFFF;
  color: #3F51B5;
  border-radius: 5px;
  font-size: 1.3em;
  font-weight: 600;
}
#main {
  background-color: #3F51B5;
}
#main .stock {
  max-width: 690px;
  margin: -1px auto 16px auto;
  padding: 64px;
  color: #2c3e50;
  background-color: #FFFFFF;
  border-radius:5px;
  -webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,0.2);
  box-shadow:0 2px 2px 0 rgba(0,0,0,0.2);
}
#main .stock:first-child {
  margin-top: -32px;
}
#main .stock .tag-buttons {
  text-align: right;
}
#main .stock .tag-buttons div {
  display: inline-block;
}
#main .stock .tag-buttons button {
  padding: 8px;
  font-size: 1rem;
  font-weight: 500;
  border: 0;
  background-color: #FFFFFF;
  color: #999999;
}
#main .stock .link-buttons .qiita-link,
#main .stock .link-buttons .agenda-button{
    padding: 8px;
    font-size: .8em;
    font-weight: 100;
    border-style: none;
    background-color: #AD1457;
    border-radius: 3px;
    color: #FFFFFF;
}
#main .stock .link-buttons .qiita-link {
    text-decoration: none;
    line-height: normal;
    display: inline-block;
}
#main .stock .link-buttons .agenda-list ul{
  word-break: break-all;
  -webkit-padding-start: 8px;
          padding-inline-start: 8px;
}
#more-show {
  text-align: center;
  padding: 24px 0 64px 0;
}
